import React, { useState } from 'react';
import axios from "axios";

const Form = () => {
    const [elements, setElements] = useState([]);
    const [data, setData] = useState({
        title: "",
        description: "",
        subject: "",
        body: []
    });
    const handleChange = (e) => {
        const name = e.target.name;
        const value = e.target.value;

        setData({ ...data, [name]: value });
    }

    const changeBody = (e, ifImg) => {
        const name = e.target.name;
        const value = e.target.value;

        let myArr = [...data.body];

        // to convert image to base64
        if (ifImg) {
            var file  = e.target.files[0];
            var reader = new FileReader();
            if (file && file.type.match('image.*')) {
              reader.readAsDataURL(file);
            } else {
                document.getElementById(`preview-${name}`).alt = "no image choosen";
            }
            reader.onloadend = function (e) {
                document.getElementById(`preview-${name}`).src = reader.result;
                myArr[name].data = reader.result;
            }
        } else {
            myArr[name].data = value;
        }
        setData({ ...data, body: myArr });
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        
        axios.post('/articles', data);
        alert('new article added successfully !');
    }
    const add = (type) => {
        setData({ ...data, body: [...data.body, {type: type, data: ""}] });
        setElements([...elements, type]);
    }

    return <>
        <section className="container">
            <h2 className='g w-100 text-center'>create new article</h2>
                <input
                    placeholder='title'
                    className='rtl form-control w-100 ff-3 my-3'
                    name="title"
                    value={data.title}
                    onChange={(e) => handleChange(e)}
                    ></input>
                <input
                    placeholder='description'
                    className='rtl form-control w-100 ff-3 my-3'
                    name="description"
                    value={data.description}
                    onChange={(e) => handleChange(e)}
                ></input>
                <input
                    placeholder='subject'
                    className='rtl form-control w-100 ff-3 my-3'
                    name="subject"
                    value={data.subject}
                    onChange={(e) => handleChange(e)}
                ></input>
            <div className="w-100">
                {
                    elements.map((a, i) => {
                        if (a === "title") {
                            return <input
                                key={i}
                                name={i}
                                onChange={(e) => changeBody(e, false)}
                                value={data.body[i].data}
                                className='rtl form-control w-100 ff-3 my-3'
                                placeholder='subtitle'/>
                        } else if (a === "p") {
                            return <textarea
                                key={i}
                                name={i}
                                onChange={(e) => changeBody(e, false)}
                                value={data.body[i].data}
                                className='rtl form-control w-100 ff-3 my-3'
                                placeholder='paragraph'/>
                        } else if (a === "code") {
                            return <textarea
                                key={i}
                                name={i}
                                onChange={(e) => changeBody(e, false)}
                                value={data.body[i].data}
                                className='ltr form-control w-100 ff-3 my-3'
                                placeholder='code'/>
                        } else if (a === "link") {
                            return <input
                                key={i}
                                name={i}
                                onChange={(e) => changeBody(e, false)}
                                value={data.body[i].data}
                                className='ltr form-control w-100 ff-3 my-3'
                                placeholder='link'/>
                        } else if (a === "img") {
                            return <div className="d-flex justify-content-center flex-wrap blog-img w-100"> 
                                <img src="" alt="img preview" id={`preview-${i}`} className="text-white text-center" />
                            <input
                                type="file"
                                key={i}
                                name={i}
                                onChange={(e) => changeBody(e, true)}
                                value={data.body[i].data}
                                className='rtl form-control w-100 ff-3 my-3'
                                placeholder='img-link'/>
                            </div> 
                        }
                    })
                }
            </div>


            <div className='w-100 d-flex justify-content-between'>
                <button className='btn btn-primary' onClick={() => add("title")}>add title</button>
                <button className='btn btn-primary' onClick={() => add("p")}>add paragraph</button>
                <button className='btn btn-primary' onClick={() => add("link")}>add link</button>
                <button className='btn btn-primary' onClick={() => add("code")}>add code</button>
                <button className='btn btn-primary' onClick={() => add("img")}>add img</button>
            </div>
            <button type="button" className="btn btn-success text-white my-3" onClick={(e) => handleSubmit(e)}>Add</button>
        </section>
    </>
}

export { Form };