import React from 'react';
import {
  BrowserRouter as Router,Routes,
Route} from "react-router-dom";

import { Header, Nav } from "./home";
import { About } from './about';
import { Projects } from './projects';
import { Contact, Footer } from './contact';
import { Blog } from './blog';
import { Form } from './form';
import { Article } from './singleArticle';

const All = () => {
  return <>
    <Header></Header>
    <About></About>
    <Projects></Projects>
    <Contact></Contact>
  </>
}

const AboutPage = () => {
  return <>
    <div className="container">
      <Nav />
    </div>
    <About/>
    <Footer/>
  </>
}
const ProjectsPage = () => {
  return <>
    <div className="container">
      <Nav />
    </div>
    <Projects />
    <Footer/>
  </>
}
const ContactPage = () => {
  return <>
    <div className="container">
      <Nav />
    </div>
    <Contact />
  </>
}
const BlogPage = () => {
  return <>
    <div className="container">
      <Nav />
    </div>
    <Blog />
    <Footer/>
  </>
}

const Error = () => {
  return <>
    <h1 className="text-center text-white">404 Page Not Found</h1>
    <a href="/" className="w-100 text-center fs-2">Back to Home Page</a>
  </>
}

function App() {
  return (<>
    <Router>
      <Routes>
        <Route path="/" element={<All/>}/>
        <Route path="/about" element={<AboutPage/>}/>
        <Route path="/projects" element={<ProjectsPage/>}/>
        <Route path="/contact" element={<ContactPage/>}/>
        <Route path="/blog" element={<BlogPage/>}/>
        <Route path="/blog/:id" element={<Article/>}/>
        <Route path="/gtav5532759/create" element={<Form/>}/>
        <Route path="*" element={<Error/>}/>
      </Routes>
    </Router>
  </>);
}

export default App;
